import React, { useState, useEffect } from 'react';
import { getQuotesBatch } from '../../apiConfig';
import { Link } from 'react-router-dom';
import { TrendingUp, TrendingDown } from 'lucide-react';
import './GlobalMarketTicker.css'; // This CSS file will also be replaced

// Helper to format change percentage
const formatPercent = (change) => {
    if (change == null || isNaN(change)) return '+0.00%';
    return `${change >= 0 ? '+' : ''}${change.toFixed(2)}%`;
};

// Helper to format absolute change
const formatAbsolute = (change) => {
    if (change == null || isNaN(change)) return '+0.00';
    return `${change >= 0 ? '+' : ''}${change.toFixed(2)}`;
};

// Define the market categories and their tickers
// NOTE: Crypto and Futures are omitted until the backend script fetches their data.
const marketTabs = {
    'MARKETS': ['^GSPC', '^IXIC', '^NSEI', 'GC=F', 'CL=F'],
    'US': ['^GSPC', 'NQW00', '^IXIC', '^RUT', '^VIX'],
    'EUROPE': ['^GDAXI', '^FTSE'],
    'INDIA': ['^NSEI', '^BSESN'],
    'COMMODITIES': ['GC=F', 'SI=F', 'CL=F'],
};

/**
 * This is the new Data Card component with the 3-column layout
 * and conditional background coloring.
 */
const DataCard = ({ quote }) => {
    if (!quote) return null;

    const isPositive = quote.changeAmount >= 0;
    const cardClass = isPositive ? 'data-card positive' : 'data-card negative';
    const iconClass = isPositive ? 'icon text-green-700' : 'icon text-red-700';
    const changeClass = isPositive ? 'change-data text-green-700' : 'change-data text-red-700';

    return (
        <Link to={`/market/${encodeURIComponent(quote.ticker)}`} className={cardClass}>
            {/* Column 1: Trend Icon */}
            <div className={iconClass}>
                {isPositive ? <TrendingUp size={24} /> : <TrendingDown size={24} />}
            </div>

            {/* Column 2: Primary Data (Left-Aligned) */}
            <div className="flex-col">
                <span className="font-bold text-gray-900 truncate">{quote.name}</span>
                <span className="text-gray-700">
                    {quote.currentPrice ? quote.currentPrice.toLocaleString('en-US', { maximumFractionDigits: 2, minimumFractionDigits: 2 }) : '...'}
                </span>
            </div>

            {/* Column 3: Change Data (Right-Aligned) */}
            <div className={`flex-col items-end ml-auto text-right ${changeClass}`}>
                <span className="font-semibold">{formatPercent(quote.changePercent)}</span>
                <span>{formatAbsolute(quote.changeAmount)}</span>
            </div>
        </Link>
    );
};

const GlobalMarketTicker = () => {
    const [activeTab, setActiveTab] = useState('MARKETS');
    const [quotes, setQuotes] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchQuotes = async () => {
            setLoading(true);
            setQuotes([]);
            try {
                const tickers = marketTabs[activeTab];
                if (!tickers || tickers.length === 0) {
                    setLoading(false);
                    return;
                }
                const response = await getQuotesBatch(tickers);

                const orderedQuotes = tickers.map(ticker =>
                    response.data.find(q => q.ticker === ticker)
                ).filter(Boolean);

                setQuotes(orderedQuotes);
            } catch (error) {
                console.error(`Failed to fetch quotes for ${activeTab}:`, error);
            } finally {
                setLoading(false);
            }
        };

        fetchQuotes();
    }, [activeTab]);

    return (
        <div className="bg-white border-b border-gray-200 sticky top-0 z-40">
            <div className="container mx-auto px-4 max-w-6xl">

                {/* Component 1: The Tabbed Filter Navigation (Pill Style) */}
                <div className="flex items-center space-x-2 py-2">
                    {Object.keys(marketTabs).map(tabName => (
                        <button
                            key={tabName}
                            onClick={() => setActiveTab(tabName)}
                            className={`px-4 py-1.5 text-sm font-semibold rounded-full transition-colors duration-200
                                ${activeTab === tabName
                                    ? 'bg-blue-100 text-blue-700'
                                    : 'text-gray-500 hover:bg-gray-100 hover:text-gray-900'
                                }
                            `}
                        >
                            {tabName}
                        </button>
                    ))}
                </div>

                {/* Component 2: The Dynamic Data Ticker Row */}
                <div className="ticker-row-container">
                    <div className="ticker-row">
                        {loading && (
                            [...Array(5)].map((_, i) => (
                                <div key={i} className="data-card skeleton">
                                    <div className="icon skeleton-line w-6 h-6"></div>
                                    <div className="flex-col w-24">
                                        <div className="skeleton-line w-20 h-4"></div>
                                        <div className="skeleton-line w-16 h-3 mt-1.5"></div>
                                    </div>
                                    <div className="flex-col items-end ml-auto w-16">
                                        <div className="skeleton-line w-12 h-4"></div>
                                        <div className="skeleton-line w-10 h-3 mt-1.5"></div>
                                    </div>
                                </div>
                            ))
                        )}
                        {!loading && quotes.map(quote => (
                            <DataCard key={quote.ticker} quote={quote} />
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default GlobalMarketTicker;